import { useSelector } from 'react-redux';
import { setWidthA, setWidthB, setDepth, setHeight, setApron} from '../../actions';

import styled from 'styled-components';

import SizeInputItem from './sizeInputItem/SizeInputItem';

import { StyledConfigurationStartTitle } from './../configurationhoice/Configurationhoice';

const StyledConfigurationStartRow = styled.div`
  display: flex;
  justify-content: space-between;
  margin-top: ${props => props.marginTop || '30px'};
`

const SizeInput = () => {
  const { widthA, depth, height, widthB, apron, configuration} = useSelector(state => state);

  let widthWallAName = '';
  let widthWallBView = null;
  if (configuration==='') {
    widthWallAName = '  "A"'
    widthWallBView = <SizeInputItem name='  "B"' value={widthB} func={setWidthB} error={false}/>
  }

  return (
    <>
      <StyledConfigurationStartTitle>  </StyledConfigurationStartTitle>
      <StyledConfigurationStartRow>
        <SizeInputItem name={widthWallAName} value={widthA} func={setWidthA} error={false}/>
        <SizeInputItem name='' value={height} func={setHeight} error={false}/>
        <SizeInputItem name='' value={depth} func={setDepth} error={false}/>
        <SizeInputItem name='' value={apron} func={setApron} error={false}/>
      </StyledConfigurationStartRow>
      <StyledConfigurationStartRow marginTop='25px'>
        {widthWallBView}
        <div style={{width: '300px'}}></div>
        <div style={{width: '300px'}}></div>
        <div style={{width: '300px'}}></div>
      </StyledConfigurationStartRow>
    </>
  );
};

export default SizeInput;